@media print {
  .no-printable {
    display: none;
  }
}

:root {
  font-size: 10px;
}
   
.rootApp {
  display: grid;
  height: 100vh;
  grid-template-rows: auto 1fr auto;
}

header.top-app-bar {
  background: #46AEDB;
  padding: 0.5rem;
  
  position: fixed;
  width: 100vw;
  box-sizing: border-box;

  display: grid;
  grid-template-columns: repeat(12, 1fr);
  align-items: baseline;
  column-gap: 5px;
}

header .top-app-bar__left-part {
  color: white;
  font-size: 2.5rem;
  grid-column: 1 / 6;
}

header .top-app-bar__right-part {
  grid-column: 8 / 12;
}

main {
  background: white;
  padding-top: 7rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

footer {
  background: white;

  position: fixed;
  width: 100vw;
  box-sizing: border-box;
  bottom: 0;
  left: 0;

  padding-top: 1em;
  padding-left: 0.5em;
  padding-right: 0.5em;
  
  display: flex;
  justify-content: space-evenly
}

