
.receipt-card-container {
  margin: 2px;
  border: 3px solid #46AEDB;
  border-radius: 10px;
  background: #f3f7ff;
  flex: 1;
}

.receipt-card-container span {
  color: #46AEDB;
  font-size: 2em;
  font-weight: bold;
}

.receipt-card-container label {
  color: #46AEDB;
  margin-bottom: 0.01rem;
  font-weight: 1;
  
  position: absolute;
  padding-top: 2px;
  padding-left: 4px;
}

.receipt-card-container input {
  padding-top: 25px;
  border-color: white;
  border-bottom-color: #46AEDB;
  margin-bottom: 0.5rem;
}

.receipt-card-container .receipt-card-payer {
  margin-bottom: 1em;
  margin-left: 1em;
  margin-right: 1em;
  padding: 0.3em;
  background-color: white;
}

.receipt-card-container .receipt-card-header {
  display: flex;
  justify-content: space-around;
  
  padding-top: 1em;
  padding-top: 1em;
  padding-left: 1em;
  padding-right: 1em;
}

.receipt-card-container .receipt-card-payer {
  border: 1px solid #46AEDB;
  border-radius: 10px;
}

.receipt-card-container .receipt-card-actions {
  display: flex;
  justify-content: space-around;
}
  
