

.receipt-container {
  border: 3px solid #46AEDB;
  border-radius: 10px;
  background: #f3f7ff;

  flex: 1;
  
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto;
}

.receipt-container span {
  color: #46AEDB;
  font-size: 2em;
  font-weight: bold;
}
.receipt-container label {
  color: #46AEDB;
  margin-bottom: 0.01rem;
  font-weight: 1;
  
  position: absolute;
  padding-top: 2px;
  padding-left: 4px;
}

.receipt-container input {
  padding-top: 25px;
  border-color: white;
  border-bottom-color: #46AEDB;
  margin-bottom: 0.5rem;
}

.receipt-container .receipt-header {
  grid-column-start: 1;
  grid-column-end: 1;
  grid-row-start: 1;
  grid-row-end: 1;
  
  display: flex;
  justify-content: space-around;
  
  padding-top: 1em;
  padding-top: 1em;
  padding-left: 1em;
  padding-right: 1em;
}

.receipt-container .receipt-payer,
.receipt-container .receipt-payee,
.receipt-container .receipt-date   {
  margin-bottom: 1em;
  margin-left: 1em;
  margin-right: 1em;
  padding: 0.3em;
  background-color: white;

  border: 1px solid #46AEDB;
  border-radius: 10px;
}
    
  